.title {
    margin-top: 50px;
}

.paragraph{
    margin-top: 10px;
    margin-bottom: 20px;
}

.resume-button {
    position: relative;
    top: 5px;
    right: -85vw;
}

.resume-container {
    display: flex;
    justify-content: space-between;
}

.education-container {
    display: flex;
    column-gap: 15px;
    margin-top: 15px;
}

.education-container img{
    max-height: 32px;
}

.info-container {
    display: block;
}

.card {
    max-width: 80vw;
    margin: 25px 0;
}

@media only screen and (min-width: 992px) {
    .card {
        max-width: 30vw;
        margin: 25px 0;
    }
}

.current-project {
    display: flex;
    align-items:center;
    justify-content: space-around;
    margin-bottom: 45px;
}

.url-group {
    display: flex;
}

.other-project {
    display: flex;
    
}

.repo-name {
    font-weight: bold;
}

.end-container {
    margin-bottom: 100px;
}
