.sticky {
    position: sticky;top: 10px; 
}

img {
    max-width: 30vw;
    height: auto;
}

.profile-img {
    max-width: 1000px;
    border-radius: 50%;
    max-width: 60%;
    margin-bottom: 5%;
}

.sidebar-section {
    margin-bottom: 20px;
    margin-top: 10px;
}

.sidebar-name {
    margin-bottom: 10px;
}

.sidebar-group {
    display: flex;
    align-items:center;
    margin-bottom: 10px;
    column-gap: 10px;
}

.org-logo {
    max-width: 32px;
}

.right-down-corner {
    position: absolute;
    bottom: -20px;
    right: 0;
    font-size: 32px;
}
